import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import Layout from '../../components/layout';
import SectionMenu from '../../components/sections/menu';
import SectionWorks from '../../components/sections/works';

const WorksPage = () => {
  const data = useStaticQuery(graphql`
    query {
      Image01: file(relativePath: { eq: "1920x1280-mainvisual.jpg" }) {
        childImageSharp {
          gatsbyImageData
        }
      }
      Image02: file(relativePath: { eq: "1920x1280-tree.jpg" }) {
        childImageSharp {
          gatsbyImageData
        }
      }
      Image03: file(relativePath: { eq: "1920x1280-wosaka.jpg" }) {
        childImageSharp {
          gatsbyImageData
        }
      }
    }
  `);

  const { Image01, Image02, Image03 } = data;

  return (
    <Layout>
      <SectionMenu />
      <SectionWorks images={[Image01, Image02, Image03]} />
    </Layout>
  );
};

export default WorksPage;
